export default {
	data() {
		return {
			getData: [],
			showView: 0
		};
	},
	props: {
		dataList: '',
	},
	mounted() {
		if (Object.prototype.toString.call(this.dataList).replace("]", "").slice(8).trim() == 'Array') {
			this.getData = this.dataList;
			this.showView = 1;
		} else {
			this.getData = this.dataList;
			console.log(this.getData);
			this.showView = 2;
		}
	},
	methods: {
		outPut(code) {
			this.$emit('code', code);
		}
	}
}
